.footer {
    &__main {
        @include section();
        background-color: $primaryColor;
    }

    &__title {
        @include h2($white);
    }

    &__contacts {
        display: flex;
        flex-direction: column;

        @media ($desktop) {
            flex-direction: row;
            justify-content: center;
        }
    }

    &__contacts-form {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        margin-bottom: 4rem;
        
        @media ($desktop) {
            flex-basis: 50rem;
            margin-bottom: 0;
            margin-right: 4.6rem;
        }
    }

    &__input {
        @extend %input;
        margin-bottom: 2.1rem;
    }

    &__textarea {
        @extend %textarea;
        margin-bottom: 2.1rem;
    }
    &__submit {
        @extend %btn;
        align-self: center;
    }

    &__contacts-items {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media ($desktop) {
            align-items: flex-start;
        }
    }
    &__links {
        display: flex;
        flex-direction: column;
        margin-bottom: 3.2rem;
        &-item {
            text-decoration: none;
            color: $white;
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: 1.8rem;

            &:last-child {
                margin-bottom: 0;
            }

            span:nth-child(2) {
                @include p1($white);
            }
        }
    }
    &__icon {
        width: 1rem;
        font-size: 1.5rem;
        margin-right: 1rem;
        &_ico {
            font-family: $iconFont;
            font-size: 1.8rem;
            margin-top: 3px;
        }
    }
    &__copyright{
        background-color: $grey3;
        padding: 0.8rem 0 0.4rem;

        &-text {
            text-align: center;
            font-size: 1.2rem;
            color: $secondaryColor;
            letter-spacing: 0.3px;
        }
    }
}