.feature {
    padding-left: 4.1rem;
    width: 100%;
    position: relative;
    &__text {
        @include p1();
    }

    &::before {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 22px;
        content: "\271A";
        position: absolute;
        color: $white;
        left: 0;
        top: 0.5rem;
        width: 32px;
        height: 32px;
        background-color: $primaryColor;
        line-height: 1.5;
    }
}