@mixin h1($_color: $white) {
    color: $_color;
    font-size: 4rem;
    font-family: $mainFont;
    text-shadow: $mainShadow;
    margin-bottom: 1.8rem;

    @media ($tablet) {
        font-size: 5.9rem;
        margin-bottom: 3rem;
    }
}

@mixin h2($_color: $primaryColor, $_align: center) {
    color: $_color;
    font-size: 3rem;
    text-align: $_align;
    line-height: 1.2;
    margin-bottom: 3rem;
    font-weight: normal;
}

@mixin h3($_color: $primaryColor) {
    color: $_color;
    font-size: 2.4rem;
    line-height: 1.25;
}

@mixin p1($_color: $textColor) {
    color: $_color;
    font-size: 1.6rem;
    line-height: 1.5;
}

@mixin p2($_color: $textColor) {
    color: $_color;
    font-size: 1.4rem;
    line-height: 1.29;
}

@mixin lead($_color: $white) {
    color: $_color;
    font-size: 2rem;
    line-height: 1.8;
    tab-size: $mainShadow;
}

@mixin img($_bg: $primaryColor) {
    background-color: $_bg;
    object-fit: cover;
    object-position: center;
}

@mixin section($_pt: 5rem, $_pb: 5rem) {
    padding-top: $_pt;
    padding-bottom: $_pb;
    padding-left: 1.5rem;
    padding-right: 1.5rem;

    @media ($projector) {
        padding-left: 0;
        padding-right: 0;
    }
}

%btn {
    display: inline-block;
    color: $primaryColor;
    background-color: $white;
    text-transform: uppercase;
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1;
    padding: 1.4rem 4.8rem;
    cursor: pointer;
    border: none;
    text-decoration: none;
    border-radius: 0.5rem;
    text-align: center;
    user-select: none;

    &:hover {
        background-color: darken($color: $white, $amount: 20%)
    }
    &:focus {
        outline: none;
    }
}

%input {
    background-color: $white;
    color: $secondaryColor;
    padding: 1.4rem 1rem;
    font-size: 1.6rem;
    line-height: 1;
    border: none;
    border-radius: 0.5rem;

    &::placeholder {
        color: $secondaryColor;
    }
}
%textarea {
    @extend %input;
    resize: none;
    height: 7rem;
    font-family: $mainFont;
    &::placeholder {
        color: $secondaryColor;
    }
}