.stage {
    @include section();
    background-color: $primaryColor;

    &__wrapper {
        display: flex;
        flex-direction: column-reverse;

        @media ($tablet) {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__title {
        @include h1();
    }

    &__list {
        @include lead();

        &-item {
            position: relative;
            list-style: none;

            &::before {
                content: "\2714";
                color: $white;
                position: absolute;
                font-size: 2rem;
                left: -3rem;
            }
        }
    }

    &__image {
        @include img($white);
        height: 21.4rem;
        width: 100%;
        margin-bottom: 2rem;

        @media ($tablet) {
            flex-shrink: 0;
            max-width: 300px;
            margin-bottom: 0;
            margin-left: 4rem;
        }
        @media ($desktop) {
            max-width: 469px;
            margin-left: 8rem;
        }
    }

}